.fees-card {
	min-width : 50%;
	padding   : 36px 36px;

	&:first-child {
		margin-bottom : 30px;
	}

	@media screen and (min-width : $break-table) {
		&:first-child {
			margin-bottom : 0;
		}
		&:not(:first-child) {
			margin-left : 20px;
		}
	}
	@media screen and (min-width : $break-desktop) {
		min-width : 303px;
	}

	&--primary {
		background-color : $primary-color;
	}

	&--dark {
		background-color : $dark-color;
	}


}

.fees-card__title {
	border-bottom  : 1px solid rgba(255, 255, 255, 0.2);
	color          : $light-color;
	font-size      : 24px;
	font-weight    : 600;
	line-height    : 32px;
	padding-bottom : 24px;
}

.fees-card__wrap {
	padding-top : 24px;
}

.fees-card__price {
	margin-bottom : 12px;
}

.fees-card__old {
	color                : $light-color;
	font-size            : 16px;
	font-weight          : 400;
	line-height          : 26px;
	text-decoration-line : line-through;
}

.fees-card__current {
	color       : $light-color;
	font-size   : 28px;
	font-weight : 700;
	line-height : 40px;
}

.fees-card__term {
	color       : $light-color;
	font-size   : 16px;
	font-weight : 400;
	line-height : 26px;
}

.fees-card__list {
	margin-bottom : 24px;

	li {
		align-items : center;
		color       : $light-color;
		display     : flex;
		font-size   : 16px;
		font-weight : 400;
		line-height : 26px;

		&:not(:last-child) {
			margin-bottom : 12px;
		}

		img {
			display      : block;
			flex-shrink  : 0;
			margin-right : 16px;
		}
	}
}
