.timeline {
	overflow : hidden;
}

.timeline__title {
	margin-bottom : 40px;
	text-align    : center;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {

	}
}

.timeline__grid {
	display               : grid;
	grid-row-gap          : 40px;
	grid-template-columns : repeat(3, 1fr);
	position              : relative;


	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		grid-column-gap       : 30px;
		grid-template-columns : repeat(12, 1fr);

		&:after {
			background-image  : url("../images/teaching-model/timeline.png");
			background-repeat : no-repeat;
			background-size   : 855px 3px;
			content           : "";
			height            : 2px;
			left              : 122px;
			position          : absolute;
			top               : 39px;
			width             : 80%;
			z-index           : -1;
		}
	}
}

.timeline__item {
	align-items    : center;
	display        : flex;
	flex-direction : row;
	grid-column    : 3 span;

	&:first-child {
		.timeline__icon:after {
			display : none;
		}
	}

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		flex-direction : column;
		text-align     : center;
	}
}

.timeline__icon {
	align-items      : center;
	background-color : #F4F5F7;
	border-radius    : 100px;
	display          : flex;
	flex-shrink      : 0;
	height           : 80px;
	justify-content  : center;
	position         : relative;
	width            : 80px;

	&:after {
		background-image  : url("../images/icon/line-dots.svg");
		background-repeat : no-repeat;
		background-size   : 10px 85px;
		bottom            : 75px;
		content           : "";
		height            : 100%;
		left              : 50%;
		opacity           : 1;
		position          : absolute;
		transform         : translateX(-50%);
		width             : 10px;
		z-index           : -1;
	}


	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 16px;
		&:after {
			display : none;
		}


	}
}


.timeline__wrap {
	margin-left : 16px;
	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-left : 0;
	}
}

.timeline__name {
	color         : $dark-color;
	font-size     : 18px;
	font-weight   : 700;
	line-height   : 28px;
	margin-bottom : 4px;
}

.timeline__about {
	color       : $dark-color;
	font-size   : 16px;
	font-weight : 400;
	line-height : 26px;

	@media screen and (min-width : $break-table) {
		font-size   : 18px;
		line-height : 28px;
	}
}
