
.roadmap__title {
	margin-bottom : 40px;
	max-width     : 650px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}

}

.roadmap__grid {
	display               : grid;
	grid-column-gap       : 30px;
	grid-row-gap          : 20px;
	grid-template-columns : repeat(3, 1fr);
	margin-bottom         : 40px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		grid-template-columns : repeat(12, 1fr);
	}
}

.roadmap__item {
	display     : flex;
	grid-column : 3 span;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		flex-direction : column;
	}
}

.roadmap__icon {
	align-items    : center;
	display        : flex;
	flex-direction : column;
	margin-right   : 16px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		flex-direction : row;
		margin-bottom  : 16px;
		margin-right   : 0;
	}

	span {
		border-right : 2px dashed #F4A2C0;
		display      : block;
		height       : 100%;
		margin       : 12px 0 0 0;
		width        : 2px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			border-bottom : 2px dashed #F4A2C0;
			height        : 1px;
			margin        : 0 24px;
			width         : 100%;
		}
	}
}

.roadmap__name {
	color       : $dark-color;
	font-size   : 20px;
	font-weight : 600;
	line-height : 30px;
}

.roadmap__about {
	color       : $dark-color;
	font-size   : 16px;
	font-weight : 400;
	line-height : 26px;
}
