.course-hero {
	background-image    : url("../images/course/hero/course-bg.webp");
	background-position : center;
	background-repeat   : no-repeat;
	background-size     : cover;
	padding             : 20px 0 48px 0;

	.no-webp & {
		background-image : url("../images/course/hero/course-bg.jpg");
	}

	@media screen and (min-width : $break-table) {
		padding : 80px 0 80px 0;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.course-hero__wrapper {

}

.course-hero__title {
	max-width : 730px;
}

.course-hero__subtitle {
	margin-bottom : 16px;
}

.course-hero__list {
	display               : grid;
	grid-gap              : 4px;
	grid-template-columns : repeat(1, 1fr);
	margin-bottom         : 36px;
	max-width             : 540px;

	@media screen and (min-width : $break-table) {
		grid-gap              : 16px 30px;
		grid-template-columns : repeat(2, 1fr);
	}
	@media screen and (min-width : $break-desktop) {

	}


	li {
		grid-column : 1 span;

		span {
			display : block;

			&:first-child {
				color          : rgba(255, 255, 255, 0.65);
				font-size      : 14px;
				font-weight    : 700;
				letter-spacing : 1px;
				line-height    : 18px;
				text-transform : uppercase;
			}

			&:last-child {
				color       : $light-color;
				font-size   : 18px;
				font-weight : 700;
				line-height : 28px;
			}
		}
	}
}

.course-hero__buttons {
	align-items    : flex-start;
	display        : flex;
	flex-direction : column;

	.b-btn + .b-btn {
		margin-left : 0;
		margin-top  : 12px;
	}


	@media screen and (min-width : $break-table) {
		flex-direction : row;

		.b-btn + .b-btn {
			margin-left : 12px;
			margin-top  : 0;
		}
	}
	@media screen and (min-width : $break-desktop) {

	}
}
