.teaching {
	background-color : $light-color;
	padding          : 48px 0 64px;


	@media screen and (min-width : $break-table) {
		padding : 56px 0 56px;
	}

	@media screen and (min-width : $break-desktop) {
	}
}

.teaching__wrapper {

	display        : flex;
	flex-direction : column-reverse;

	@media screen and (min-width : $break-table) {
		margin    : 0 auto;
		max-width : 532px;
	}

	@media screen and (min-width : $break-desktop) {
		align-items     : flex-start;
		flex-direction  : row;
		justify-content : space-between;
		margin          : 0;
		max-width       : none;
	}
}

.teaching__left {

	position : relative;

	&:after {
		background-image  : url("../images/icon/slash-yellow.svg");
		background-repeat : no-repeat;
		background-size   : cover;
		content           : "";
		display           : none;
		height            : 120px;
		position          : absolute;
		width             : 50px;
		z-index           : 1;
	}

	@media screen and (min-width : $break-table) {
		&:after {
			bottom  : 56px;
			display : block;
			right   : -82px;
		}
	}

	@media screen and (min-width : $break-desktop) {
		padding-left : 95px;

		&:after {
			bottom : unset;
			left   : 0;

			right  : unset;
			top    : 0;
		}
	}

}

.teaching__title {
	color         : $dark-color;
	font-size     : 28px;
	font-weight   : 700;
	line-height   : 40px;
	margin-bottom : 32px;
	max-width     : 400px;
	@media screen and (min-width : $break-table) {
		font-size   : 48px;
		font-weight : 800;
		line-height : 64px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.teaching-card {
	background-color : $yellow;
	margin-bottom    : 20px;
	padding          : 16px 16px;

	@media screen and (min-width : $break-table) {
		max-width : 445px;
		padding   : 32px 32px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 0;
	}
}

.teaching-card__top {
	align-items   : flex-start;
	display       : flex;
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 32px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.teaching-card__img {
	border-radius : 100%;
	height        : 56px;
	overflow      : hidden;
	width         : 56px;

	img {
		height     : 100%;
		object-fit : cover;
		width      : 100%;
	}
}

.teaching-card__wrap {
	margin-left : 16px;
}

.teaching-card__name {
	color       : $dark-color;
	display     : block;
	font-size   : 18px;
	font-weight : 700;
	line-height : 28px;
}

.teaching-card__prof {
	color       : $dark-color;
	font-size   : 14px;
	font-weight : 400;
	line-height : 24px;
}

.teaching-card__about {
	color        : $dark-color;
	font-size    : 18px;
	font-weight  : 400;
	line-height  : 32px;
	padding-left : 32px;
	position     : relative;

	&:after {
		background-image  : url("../images/icon/quotes.svg");
		background-repeat : no-repeat;
		background-size   : cover;
		content           : "";
		height            : 14px;
		left              : 0;
		position          : absolute;
		top               : 0;
		width             : 16px;
		z-index           : 1;
	}
}
