.course-nav {
	background-color : $light-color;
	padding-top      : 24px;
	position         : sticky;
	top              : 56px;
	z-index          : 10;

	@media screen and (min-width : $break-table) {
		top : 72px;
	}
}

.course-nav__list {
	align-items     : center;
	border-bottom   : 2px solid #D9DBE1;
	display         : flex;
	justify-content : space-between;
	margin-right    : -20px;
	overflow-x      : auto;

	@media screen and (min-width : $break-table) {
		margin-right : 0;
	}

	@media screen and (min-width : $break-desktop) {
		width : 100%;
	}
}

.course-nav__item {
	padding  : 10px 20px;
	position : relative;
	width    : 100%;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding : 16px 0;
		width   : 100%;
	}

	a {
		color          : rgba(0, 0, 0, 0.6);
		flex-shrink    : 0;
		font-size      : 16px;
		font-weight    : 600;
		letter-spacing : 0.3px;
		line-height    : 24px;
		min-width      : 113px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			min-width : auto;
			padding   : 16px 0;
			width     : 100%;
		}

		&.active {
			&:before {
				background-color : $blue;
				bottom           : 0;
				content          : "";
				height           : 2px;
				left             : 0;
				position         : absolute;
				width            : 100%;
				z-index          : 2;
			}

			a {
				color : $dark-color;
			}
		}
	}

}
