.faculty-hero {
	background-image    : url("../images/teaching-model/hero/teaching-bg-tablet.webp");
	background-position : center;
	background-repeat   : no-repeat;
	background-size     : cover;
	padding             : 20px 0 48px 0;

	.no-webp & {
		background-image : url("../images/teaching-model/hero/teaching-bg-tablet.jpg");
	}

	@media screen and (min-width : $break-table) {
		background-image : url("../images/faculty/faculty-bg.webp");
		padding          : 80px 0 80px 0;

		.no-webp & {
			background-image : url("../images/faculty/faculty-bg.jpg");
		}
	}

	@media screen and (min-width : $break-desktop) {
		background-size : cover;
		padding         : 80px 0 150px 0;
	}
}

.faculty-hero__title {
	max-width : 700px;
}

.faculty-hero__text {
	max-width : 730px;
}
