.faq-hero {
	background-image  : url("../images/faq/hero/faq-bg.webp");
	background-repeat : no-repeat;
	background-size   : cover;
	padding           : 20px 0 48px 0;

	.no-webp & {
		background-image : url("../images/faq/hero/faq-bg.jpg");
	}

	@media screen and (min-width : $break-table) {
		padding : 80px 0 80px 0;
	}
	@media screen and (min-width : $break-desktop) {
		background-size : cover;
	}
}

.faq-hero__title {
	max-width : 700px;
}

.faq-hero__text {
	max-width : 730px;
}
