.accreditation {
	background-color : $light-color;
}

.accreditation__wrapper {

	@media screen and (min-width : $break-table) {
		align-items     : center;
		display         : flex;
		justify-content : space-between;
		max-width       : 905px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.accreditation__left {
	margin-bottom : 20px;
	max-width     : 440px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 0;

	}

	@media screen and (min-width : $break-desktop) {
		max-width : 540px;
	}
}

.accreditation__title {
	color         : $dark-color;
	font-size     : 40px;
	font-weight   : 800;
	line-height   : 54px;
	margin-bottom : 6px;

	@media screen and (min-width : $break-table) {
		font-size   : 48px;
		line-height : 64px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.accreditation__text {
	color       : $dark-color;
	font-size   : 18px;
	font-weight : 400;
	line-height : 32px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}
