.learning {
	background-color : $light-color;
}

.learning__title {
	font-size     : 28px;
	font-weight   : 700;
	line-height   : 40px;
	margin-bottom : 36px;

	@media screen and (min-width : $break-table) {
		font-size     : 48px;
		font-weight   : 800;
		line-height   : 64px;
		margin-bottom : 48px;
	}
}

.learning__grid {
	display               : grid;
	grid-row-gap          : 24px;
	grid-template-columns : repeat(3, 1fr);

	@media screen and (min-width : $break-table) {
		grid-column-gap       : 30px;
		grid-row-gap          : 36px;
		grid-template-columns : repeat(8, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-row-gap          : 48px;
		grid-template-columns : repeat(12, 1fr);
	}
}

.learning__item {
	display     : flex;
	grid-column : 4 span;

	.b-btn--text-primary {
		display     : none;
		font-size   : 14px;
		line-height : 20px;
	}

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		flex-direction : column;

		.b-btn--text-primary {
			display : flex;
		}
	}

}

.learning__icon {
	flex-shrink   : 0;
	height        : 32px;
	margin-bottom : 24px;
	margin-right  : 24px;

	width         : 32px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-right : 0;
	}
}

.learning__content {
	align-items    : flex-start;
	display        : flex;
	flex-direction : column;
}

.learning__name {
	color         : $dark-color;
	font-size     : 24px;
	font-weight   : 600;
	line-height   : 32px;
	margin-bottom : 8px;
}

.learning__text {
	color         : $dark-color;
	font-size     : 16px;
	font-weight   : 400;
	line-height   : 26px;
	margin-bottom : 24px;
}
