.accordion-wrap {
	&:not(:last-child) {
		margin-bottom : 24px;
	}
}

.accordion {
	background-color : $light-color;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		background-color : #F4F5F7;
	}

	& + & {
		margin-top : 10px;
	}

	&.active {
		border-bottom : 1px solid rgba(0, 0, 0, 0.2);

		.accordion__content--term {
			margin-top : 12px;
		}
	}

	&--term {
		background-color : #F4F5F7;
		border-bottom    : 1px solid rgba(0, 0, 0, 0.2);
		padding          : 16px 20px;

		@media screen and (min-width : $break-table) {
			padding : 16px 32px;
		}

		@media screen and (min-width : $break-desktop) {
			border-bottom : none;
			padding       : 24px 24px;
		}

	
	}
}


.accordion__title {
	align-items     : center;
	border-bottom   : 1px solid rgba(0, 0, 0, 0.2);
	color           : #000;
	cursor          : pointer;
	display         : flex;
	font-size       : 14px;
	font-weight     : 600;
	justify-content : space-between;
	letter-spacing  : 0.3px;
	line-height     : 20px;
	outline         : none;
	padding         : 10px 20px;
	position        : relative;
	text-align      : left;
	width           : 100%;

	img {
		transform  : rotate(90deg);
		transition : transform .2s ease-in-out;
	}

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
	}

	&.active {
		border-bottom : none;


		& + .accordion__content {
			max-height : 1000px;
		}

		img {
			transform  : rotate(270deg);
			transition : transform .2s ease-in-out;
		}
	}

	&--term {
		border         : none;
		color          : $dark-color;
		cursor         : default;
		font-size      : 18px;
		font-weight    : 700;
		line-height    : 28px;
		margin-bottom  : 0;
		padding        : 0;
		pointer-events : unset;


		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			padding        : 0 0 12px 0;
			pointer-events : none;

			img {
				display : none;
			}
		}
	}
}

.accordion__content {
	background-color : $light-color;
	margin           : 0 auto;
	max-height       : 0;
	overflow         : hidden;
	transition       : all 0.3s;
	width            : 100%;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		background-color : #F4F5F7;
	}

	&--term {
		border     : none;
		max-height : 0;
		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			max-height : 1000px;
		}
	}
}


.accordion__descr {
	padding : 0 20px 10px 20px;

	&--term {
		padding : 0;
	}
}

