.header {
	background-color : rgba(0, 0, 0, 0.9);
	display          : flex;
	left             : 0;
	min-height       : 58px;
	padding          : 12px 0;
	position         : sticky;
	top              : 0;
	width            : 100%;
	z-index          : 100;

	@media screen and (min-width : $break-table) {
		min-height : 72px;
		padding    : 16px 0;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.header__wrapper {
	align-items     : center;
	display         : flex;
	justify-content : flex-end;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		justify-content : space-between;
	}
}

.header__logo {
	flex-shrink : 0;
	height      : 24px;
	width       : 123px;
	@media screen and (min-width : $break-table) {
		height : 32px;
		width  : 164px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.header__left {
	align-items : center;
	display     : flex;

	left        : 20px;
	position    : absolute;
	top         : 50%;
	transform   : translateY(-50%);

	@media screen and (min-width : $break-desktop) {
		left      : unset;
		position  : unset;
		top       : unset;
		transform : unset;
	}
}

.header__panel {
	background-color : $dark-color;
	height           : calc(100vh - 58px);
	left             : -100%;
	position         : absolute;
	top              : 58px;
	transition       : left 0.200s ease-in-out;
	width            : 100%;
	z-index          : 100;

	&.menu--active {
		left       : 0;
		transition : left 0.200s ease-in-out;
	}

	@media screen and (min-width : $break-table) {

		align-items      : center;
		background-color : transparent;
		display          : flex;
		height           : 100%;
		justify-content  : flex-end;
		left             : 0;
		overflow         : visible;
		padding          : 0;
		position         : unset;
		top              : 0;
		width            : auto;
	}

	@media screen and (min-width : $break-desktop) {
		align-items      : center;
		background-color : transparent;
		display          : flex;
		height           : auto;
		justify-content  : space-between;
		left             : unset;
		margin-left      : 32px;
		position         : unset;
		top              : unset;
		width            : 100%;
	}
}


.header__lang {
	border-bottom : 1px solid rgba(255, 255, 255, 0.2);
	border-top    : 1px solid rgba(255, 255, 255, 0.2);
	color         : $light-color;
	width         : 100%;

	@media screen and (min-width : $break-table) {
		border : none;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 0;
		width   : auto;
	}
}

.header__buttons {
	align-items : center;
	display     : flex;

	@media screen and (min-width : $break-table) {
		margin-left : 15px;
	}
	@media screen and (min-width : $break-desktop) {

	}

	.b-btn + .b-btn {
		margin-left : 8px;

		@media screen and (min-width : $break-table) {
			margin-left : 12px;
		}
		@media screen and (min-width : $break-desktop) {

		}
	}

	.b-btn--icon-secondary-transparent {
		padding : $btn-padding-icon-md;

		svg {
			height         : 12px;
			pointer-events : none;
			width          : 12px;
		}

		@media screen and (min-width : $break-table) {
			padding : 9px;
			svg {
				height : 20px;
				width  : 20px;
			}
		}
		@media screen and (min-width : $break-desktop) {

		}
	}

	.b-btn--secondary {
		font-size   : 12px;
		line-height : 16px;
		padding     : 8px 12px;


		@media screen and (min-width : $break-table) {
			font-size   : 16px;
			line-height : 24px;
			padding     : 7px 20px;
		}
		@media screen and (min-width : $break-desktop) {

		}
	}
}

.nav__list {
	align-items : center;


	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		display : flex;
	}
}


.header__logo {
	color       : $light-color;
	margin-left : 8px;

	@media screen and (min-width : $break-table) {
		margin-left : 24px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-left : 0;
	}
}

.logo {
	display    : block;
	fill       : currentColor;
	height     : 24px;
	max-height : none;
	object-fit : cover;
	width      : 123px;

	@media screen and (min-width : $break-table) {
		height : 32px;
		width  : 164px;

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.header__wrap {
	align-items     : flex-start;
	display         : flex;
	flex-direction  : column;
	height          : 100%;
	justify-content : flex-start;
	overflow-y      : auto;
	width           : 100%;

	@media screen and (min-width : $break-table) {
		overflow-y : visible;
	}

	@media screen and (min-width : $break-desktop) {
		align-items     : center;
		flex-direction  : row;
		justify-content : space-between;
		overflow-y      : visible;
	}
}


.header__nav {
	width : 100%;

}

.nav {

	@media screen and (min-width : $break-table) {
		background-color : $dark-color;
		height           : calc(100vh - 72px);
		left             : -100%;
		position         : absolute;
		top              : 72px;
		transition       : left 0.200s ease-in-out;
		width            : 100%;
		z-index          : 100;
	}
	@media screen and (min-width : $break-desktop) {
		background-color : transparent;
		height           : auto;
		left             : 0;
		position         : unset;
		top              : 0;
		transition       : unset;
		width            : 100%;
		z-index          : unset;
	}


	&--active {
		left       : 0;
		transition : left 0.200s ease-in-out;
	}

	&__list {
		height     : 100%;
		overflow-y : auto;

		@media screen and (min-width : $break-desktop) {
			height     : auto;
			overflow-y : visible;
		}

	}
}

.nav__item {
	border-top : 1px solid rgba(255, 255, 255, 0.2);
	padding    : 16px 20px;
	position   : relative;

	&:last-child {
		border-bottom : 1px solid rgba(255, 255, 255, 0.2);
	}

	&:nth-child(3) {
		padding : 0;
	}

	@media screen and (min-width : $break-table) {
		&:last-child {
			border-bottom : none;
		}
	}

	@media screen and (min-width : $break-desktop) {
		border  : none;
		padding : 0;

		&:not(:first-child) {
			margin-left : 28px;
		}

		&:last-child {
			border-bottom : none;
		}
	}

	button {
		background-color : transparent;
		border           : none;
		cursor           : pointer;
		display          : none;
		padding          : 0;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			display : block;
		}
	}

	&--button {
		display : none;

		@media screen and (min-width : $break-table) {
			display : block;
		}

		@media screen and (min-width : $break-desktop) {
			display : none;
		}

	}
}

.nav__link {
	align-items     : center;
	color           : $light-color;
	display         : flex;
	font-size       : 14px;
	font-weight     : 600;
	justify-content : space-between;
	letter-spacing  : 0.3px;
	line-height     : 20px;
	position        : relative;


	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		display : inline-block;
		&:after {
			background-color : $light-color;
			bottom           : 0;
			content          : "";
			height           : 1px;
			left             : 0;
			position         : absolute;
			transition       : width .3s ease-in-out;
			width            : 0;
		}

		&:hover {

			&:after {
				width : 100%;
			}
		}

		&.active {
			&:after {
				width : 100%;
			}
		}
	}


	svg {
		fill           : currentColor;
		margin-left    : 8px;
		pointer-events : none;
	}

	.nav__arrow-mob {
		fill           : currentColor;
		pointer-events : none;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			display : none;
		}
	}
}

.nav__submenu {

	@media screen and (min-width : $break-table) {
	}

	@media screen and (min-width : $break-desktop) {
		background-color : rgba(0, 0, 0, 0.9);
		left             : 50%;
		opacity          : 0;
		padding          : 12px 12px;
		position         : absolute;
		top              : 46px;
		transform        : translateX(-50%);
		transition       : opacity 0.200s ease-in-out;
		visibility       : hidden;
		width            : 240px;
		z-index          : 1;
	}

	&.active {
		opacity    : 1;
		visibility : visible;
	}
}

.nav__sublist {
	li {
		padding : 16px 20px;

		&:not(:first-child) {
			border-top : 1px solid rgba(255, 255, 255, 0.2);
		}

		@media screen and (min-width : $break-table) {
		}

		@media screen and (min-width : $break-desktop) {
			border  : none;
			padding : 4px 8px;
			&:not(:last-child) {
				margin-bottom : 4px;
			}

			&:not(:first-child) {
				border-top : none;
			}
		}
	}
}

.lang-panel {
	position : relative;

	&__link {
		align-items     : center;
		display         : flex;
		font-size       : 14px;
		font-weight     : 600;
		justify-content : space-between;
		line-height     : 20px;
		padding         : 15px 20px;
		transition      : transform 0.200s ease-in-out;
		width           : 100%;

		@media screen and (min-width : $break-table) {
			padding : 0;

			span {
				display     : flex;
				flex-shrink : 0;
			}
		}

		@media screen and (min-width : $break-desktop) {
			&:hover {
				span {
					&:after {
						width : 100%;
					}
				}
			}
			span {
				position : relative;

				&:after {
					background-color : $light-color;
					bottom           : 0;
					content          : "";
					height           : 1px;
					left             : 0;
					position         : absolute;
					transition       : width .3s ease-in-out;
					width            : 0;
				}
			}
		}


		.icon-drop {
			margin-left : 8px;
			width       : 24px;

			svg {
				fill       : currentColor;
				height     : 24px;
				transform  : rotate(0deg);
				transition : transform 0.200s ease-in-out;
				width      : 13px;

				@media screen and (min-width : $break-table) {
					height     : auto;
					transform  : rotate(0deg);
					transition : unset;
					width      : auto;
				}
			}
		}
	}

	&__btn {
		padding  : 0;
		position : relative;

		&:before {
			background-color : #FFF;
			bottom           : 0;
			content          : "";
			height           : 1px;
			left             : 0;
			position         : absolute;
			transition       : width .3s ease-in-out;
			width            : 0;
		}

		&:hover:before {
			width : 100%;
		}

	}

	button {
		background-color : transparent;
		border           : none;
		color            : $light-color;
		cursor           : pointer;
		font-size        : 12px;
		font-weight      : 600;
		letter-spacing   : 0.3px;
		line-height      : 16px;

		&:hover {

		}

		img {
			margin-right : 8px;
			width        : 16px;
		}
	}

	&__dropdown {
		background-color : #181920;
		box-sizing       : content-box;
		max-height       : 0;
		opacity          : 0;
		overflow         : hidden;
		transition       : max-height 0.200s ease-in-out;
		will-change      : max-height;
		@media screen and (min-width : $break-table) {
			background-color : $dark-color;
			left             : 50%;
			opacity          : 0;
			padding          : 12px 12px;
			position         : absolute;
			top              : 44px;
			transform        : translateX(-50%);
			transition       : opacity 0.200s ease-in-out;
			visibility       : hidden;
			width            : 161px;
			z-index          : 100;
		}

		@media screen and (min-width : $break-desktop) {
			border  : none;
			padding : 4px 8px;
			&:not(:last-child) {
				margin-bottom : 4px;
			}

			&:not(:first-child) {
				border-top : none;
			}
		}


		ul {
			li {

				@media screen and (min-width : $break-table) {
					&:not(:last-child) {
						margin-bottom : 12px;
					}
				}

			}
		}
	}

	&.is-open {
		.lang-panel__dropdown {
			opacity    : 1;
			padding    : 16px 20px;
			transition : max-height 0.200s ease-in-out;
			visibility : visible;

			@media screen and (min-width : $break-table) {
				padding    : 12px 12px;
				transition : opacity 0.200s ease-in-out;
			}

			@media screen and (min-width : $break-desktop) {
			}
		}

		.lang-panel__link {
			@media screen and (min-width : $break-desktop) {
				span {
					&:after {
						width : 100%;
					}
				}
			}
		}

		.icon-drop {

			svg {
				transform : rotate(180deg);

				@media screen and (min-width : $break-table) {
					transform : rotate(0deg);
				}
			}
		}
	}
}

.header--light {
	background-color : $light-color;

	.nav__link {
		color : $dark-color;

		&:after {
			background-color : $dark-color;
		}
	}

	.header__logo {
		color : $dark-color;
	}

	.nav__submenu {
		background-color : $light-color;
	}

	.header__lang {
		border-bottom : 1px solid rgba(0, 0, 0, 0.2);
		border-top    : 1px solid rgba(0, 0, 0, 0.2);

		@media screen and (min-width : $break-table) {
			border : none;
		}
	}

	.b-btn--icon-secondary-transparent {
		border-color : #8D67EF;
		color        : $primary-color;

		&:hover {
			background-color : transparent;
			border-color     : #6734EA;
		}
	}

	.b-btn--secondary {
		background-color : $primary-color;
		color            : $light-color;

		&:hover {
			background-color : #8D67EF;
			color            : #FFF;
		}
	}

	.burger-wrapper {
		border-color : $dark-color;
	}

	.burger {
		color : $dark-color;
	}

	.header__panel {
		background-color : $light-color;
	}

	.nav {
		background-color : #FFF;
	}

	.nav__item {
		border-top : 1px solid rgba(0, 0, 0, 0.2);

		&:last-child {
			border-bottom : 1px solid rgba(0, 0, 0, 0.2);
		}


		@media screen and (min-width : $break-desktop) {

			border-top : none;

			&:last-child {
				border-bottom : none;
			}
		}
	}

	.nav__sublist {
		li {
			&:not(:first-child) {
				border-top : 1px solid rgba(0, 0, 0, 0.2);
			}

			@media screen and (min-width : $break-desktop) {

				&:not(:first-child) {
					border : none;
				}
			}
		}
	}


	.lang-panel {

		&__link {
			span {
				&:after {
					background-color : $dark-color;
				}
			}

			.icon-drop {
				svg {
					fill : currentColor;
				}
			}
		}

		button {
			color : $dark-color;

		}

		.lang-panel__btn:before {
			background-color : $dark-color;
		}

		.lang-panel__dropdown {
			background-color : #F3F4F6;

			@media screen and (min-width : $break-desktop) {
				background-color : $light-color;
			}
		}

	}
}

.burger-wrapper {
	align-items     : center;
	border          : 1px solid $light-color;
	display         : flex;
	height          : 28px;
	justify-content : center;
	padding         : 10px 10px;
	width           : 28px;

	@media screen and (min-width : $break-table) {
		height  : 40px;
		padding : 20px 15px;
		width   : 40px;
	}
	@media screen and (min-width : $break-desktop) {
		display : none;
	}
}

.burger {
	background-color : transparent;
	border           : none;
	color            : $light-color;
	cursor           : pointer;
	flex-shrink      : 0;
	height           : 16px;
	padding          : 0;
	position         : relative;
	width            : 15px;


	&::before,
	&::after {
		background-color : currentColor;
		content          : "";
		height           : 2px;
		left             : 0;
		position         : absolute;
		transition       : transform 0.3s ease-in-out, top 0.3s ease-in-out;
		width            : 100%;
	}

	&::before {
		top : 2px;
	}

	&::after {
		top : calc(100% - 4px);
	}

	&__line {
		background-color : currentColor;
		border-radius    : 10px;
		height           : 2px;
		left             : 0;
		position         : absolute;
		top              : 50%;
		transform        : translateY(-50%);
		transition       : transform 0.3s ease-in-out;
		width            : 100%;
	}

	&--active {
		&::before {
			top        : 50%;
			transform  : rotate(45deg);
			transition : transform 0.3s ease-in-out, top 0.3s ease-in-out;
		}

		&::after {
			top        : 50%;
			transform  : rotate(-45deg);
			transition : transform 0.3s ease-in-out, top 0.3s ease-in-out;
		}

		.burger__line {
			transform  : scale(0);
			transition : transform 0.3s ease-in-out;
		}
	}
}

.btn-mobile {
	padding : 20px 20px;

	@media screen and (min-width : $break-table) {
		display : none;
	}
	@media screen and (min-width : $break-desktop) {

	}

	&--tablet {
		display : none;
		@media screen and (min-width : $break-table) {
			display : block;
			padding : 0;
		}
	}
}
