.academics {
}

.academics__top {
	margin-bottom : 24px;
	position      : relative;

	@media screen and (min-width : $break-desktop) {
		&:after {
			background-image  : url("../images/icon/less-icon.svg");
			background-repeat : no-repeat;
			background-size   : 88px 94px;
			content           : "";
			height            : 94px;
			position          : absolute;
			right             : 0;
			top               : 0;
			width             : 88px;
			z-index           : 1;
		}
	}
}

.academics__title {
	margin-bottom : 8px;
	max-width     : 635px;
}

.academics__subtitle {
	color       : $dark-color;
	font-size   : 18px;
	font-weight : 400;
	line-height : 32px;
	max-width   : 635px;
}

.academics__wrapper {
	background-color      : #F0F0F5;
	display               : grid;
	grid-column-gap       : 30px;
	grid-row-gap          : 40px;
	grid-template-columns : repeat(6, 1fr);
	padding               : 20px 20px;

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(12, 1fr);
		padding               : 40px 40px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.academics__item {
	display        : flex;
	flex-direction : column;
	grid-column    : 6 span;
}

.academics__info {
	align-items   : center;
	display       : flex;
	margin-bottom : 12px;
}

.academics__photo {
	border-radius : 100%;
	flex-shrink   : 0;
	height        : 56px;
	margin-right  : 16px;
	overflow      : hidden;
	width         : 56px;

	img {
		height     : 100%;
		object-fit : cover;
		width      : 100%;
	}
}

.academics__name {
	color       : $dark-color;
	font-size   : 18px;
	font-weight : 700;
	line-height : 28px;
}

.academics__jobtitle {
	color       : $dark-color;
	font-size   : 14px;
	font-weight : 400;
	line-height : 24px;
}

.academics__text {
	color         : $dark-color;
	font-size     : 14px;
	font-weight   : 400;
	line-height   : 24px;
	margin-bottom : 12px;
}

.academics__job {
	align-items : center;
	display     : flex;
	margin-top  : auto;

	div {
		display : flex;
		width   : 84px;

		&:last-child {
			margin-left : 24px;
		}

		img {
			height     : 100%;
			object-fit : cover;
			width      : 100%;
		}
	}
	
}

