.footer__copyright {
  color: #D9DBE1;
  font-size: 14px;
  font-weight: 400;
  grid-column: 1/7;
  grid-row: 5/5;
  line-height: 24px;
}
@media screen and (min-width: 681px) {
  .footer__copyright {
    grid-column: 1/12;
    grid-row: 3/3;
  }
}

.footer__subnav {
  display: flex;
  flex-direction: column;
  grid-column: 1/4;
  grid-row: 6/6;
}
@media screen and (min-width: 681px) {
  .footer__subnav {
    flex-direction: row;
    grid-column: 1/12;
    grid-row: 4/4;
  }
}
.footer__subnav li {
  position: relative;
}
.footer__subnav li:not(:last-child) {
  margin-bottom: 8px;
}
@media screen and (min-width: 681px) {
  .footer__subnav li:not(:first-child) {
    margin-bottom: 0;
    margin-left: 21px;
  }
  .footer__subnav li:not(:first-child):after {
    background-color: #969BAB;
    content: "";
    height: 12px;
    left: -11px;
    position: absolute;
    top: 8px;
    width: 1px;
  }
  .footer__subnav li:not(:last-child) {
    margin-bottom: unset;
  }
}
.footer__subnav li a {
  color: #969BAB;
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
}

.footer__accreditation {
  grid-column: 1/3;
  grid-row: 3/3;
}
@media screen and (min-width: 681px) {
  .footer__accreditation {
    grid-column: 12/13;
    grid-row: 1/3;
  }
}
.footer__accreditation div {
  color: #D9DBE1;
  font-size: 18px;
  font-weight: 600;
  line-height: 28px;
  margin-bottom: 16px;
}
.footer__accreditation img {
  -webkit-filter: grayscale(100%);
  filter: grayscale(100%);
}

.footer__social {
  grid-column: 1/3;
  grid-row: 4/4;
}
@media screen and (min-width: 681px) {
  .footer__social {
    grid-column: 12/13;
    grid-row: 4/4;
  }
}

.s-block {
  display: flex;
}
.s-block__link {
  height: 24px;
  width: 24px;
}
.s-block__link:not(:first-child) {
  margin-left: 16px;
}

.accordion-mini__content {
  box-sizing: content-box;
  color: #000;
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
  max-height: 0;
  opacity: 0;
  overflow: hidden;
  padding: 0 20px;
  transition: all 0.3s ease-out;
  will-change: max-height;
}
@media screen and (min-width: 681px) {
  .accordion-mini__content {
    font-size: 18px;
    line-height: 32px;
    padding: 0 32px;
  }
}
.accordion-mini__control {
  align-items: center;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  color: #000;
  cursor: pointer;
  display: flex;
  font-size: 14px;
  font-weight: 600;
  justify-content: space-between;
  letter-spacing: 0.3px;
  line-height: 20px;
  padding: 10px 20px;
  text-align: left;
  transition: transform 0.3s ease, background-color 0.3s ease;
  width: 100%;
}
.accordion-mini__control:hover {
  background-color: #F4F5F7;
}
@media screen and (min-width: 681px) {
  .accordion-mini__control {
    font-size: 16px;
    line-height: 24px;
    padding: 16px 32px;
  }
}
.accordion-mini__icon {
  transition: transform 0.3s ease;
}

.accordion-mini.open .accordion-mini__content {
  opacity: 1;
  padding: 0 20px 10px 20px;
}
@media screen and (min-width: 681px) {
  .accordion-mini.open .accordion-mini__content {
    padding: 0 32px 16px 32px;
  }
}

.accordion-mini.open .accordion-mini__control {
  border-bottom: transparent;
}

.accordion-mini.open .accordion-mini__content {
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

.accordion-mini.open .accordion-mini__icon {
  transform: rotate(180deg);
}

.fees-card {
  min-width: 50%;
  padding: 36px 36px;
}
.fees-card:first-child {
  margin-bottom: 30px;
}
@media screen and (min-width: 681px) {
  .fees-card:first-child {
    margin-bottom: 0;
  }
  .fees-card:not(:first-child) {
    margin-left: 20px;
  }
}
@media screen and (min-width: 1081px) {
  .fees-card {
    min-width: 303px;
  }
}
.fees-card--primary {
  background-color: #6934EA;
}
.fees-card--dark {
  background-color: #000;
}

.fees-card__title {
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  color: #FFF;
  font-size: 24px;
  font-weight: 600;
  line-height: 32px;
  padding-bottom: 24px;
}

.fees-card__wrap {
  padding-top: 24px;
}

.fees-card__price {
  margin-bottom: 12px;
}

.fees-card__old {
  color: #FFF;
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
  text-decoration-line: line-through;
}

.fees-card__current {
  color: #FFF;
  font-size: 28px;
  font-weight: 700;
  line-height: 40px;
}

.fees-card__term {
  color: #FFF;
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
}

.fees-card__list {
  margin-bottom: 24px;
}
.fees-card__list li {
  align-items: center;
  color: #FFF;
  display: flex;
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
}
.fees-card__list li:not(:last-child) {
  margin-bottom: 12px;
}
.fees-card__list li img {
  display: block;
  flex-shrink: 0;
  margin-right: 16px;
}

.banner {
  background-color: #0048FF;
  bottom: 0;
  display: none;
  left: 0;
  padding: 16px 0;
  position: fixed;
  width: 100%;
  z-index: 80;
}
.banner.active {
  display: block;
}

.banner__wrapper {
  align-items: center;
  display: grid;
  grid-gap: 16px;
  grid-template-columns: repeat(4, 1fr);
}
@media screen and (min-width: 681px) {
  .banner__wrapper {
    grid-template-columns: repeat(12, 1fr);
  }
}
@media screen and (min-width: 1081px) {
  .banner__wrapper {
    grid-gap: 30px;
  }
}

.banner__wrap {
  align-items: flex-start;
  display: flex;
  flex-direction: column;
  grid-column: 1/4;
}
@media screen and (min-width: 681px) {
  .banner__wrap {
    grid-column: 2/11;
  }
}
@media screen and (min-width: 1081px) {
  .banner__wrap {
    align-items: center;
    flex-direction: row;
    grid-column: 3/11;
    justify-content: space-between;
  }
}
.banner__wrap .b-btn--outline-white {
  padding: 10px 20px;
}

.banner__info {
  margin-bottom: 12px;
}
@media screen and (min-width: 1081px) {
  .banner__info {
    margin-bottom: 0;
  }
}

.banner__title {
  color: rgba(255, 255, 255, 0.65);
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 1px;
  line-height: 18px;
  text-transform: uppercase;
}

.banner__text {
  color: #FFF;
  font-size: 14px;
  font-weight: 600;
  line-height: 24px;
}

.banner__dots {
  display: none;
}
@media screen and (min-width: 681px) {
  .banner__dots {
    align-self: start;
    display: block;
    grid-column: 1/1;
    margin-left: 23px;
  }
}
@media screen and (min-width: 1081px) {
  .banner__dots {
    align-self: center;
    grid-column: 2/2;
    margin-left: 49px;
  }
}

.banner__close {
  align-self: start;
  background-color: transparent;
  border: none;
  cursor: pointer;
  grid-column: 4/4;
  height: 24px;
  margin-left: 38px;
  padding: 0;
  width: 24px;
}
@media screen and (min-width: 681px) {
  .banner__close {
    grid-column: 12/12;
  }
}
@media screen and (min-width: 1081px) {
  .banner__close {
    align-self: center;
  }
}
.banner__close img {
  pointer-events: none;
}