.teaching-hero {
	background-image    : url("../images/teaching-model/hero/teaching-bg-tablet.webp");
	background-position : center;
	background-repeat   : no-repeat;
	background-size     : cover;
	padding             : 20px 0 48px 0;

	.no-webp & {
		background-image : url("../images/teaching-model/hero/teaching-bg-tablet.jpg");
	}

	@media screen and (min-width : $break-table) {
		padding : 80px 0 80px 0;
	}

	@media screen and (min-width : $break-desktop) {
		background-image : url("../images/teaching-model/hero/teaching-bg.webp");
		background-size  : cover;
		
		.no-webp & {
			background-image : url("../images/teaching-model/hero/teaching-bg.jpg");
		}
	}

	.b-btn--secondary {
		font-size     : $btn-font-size-big;
		line-height   : 24px;
		margin-bottom : 16px;
		padding       : $btn-padding-big;
	}
}

.teaching-hero__title {
	margin-bottom : 16px;
	max-width     : 500px;
}

.teaching-hero__text {
	max-width : 730px;
}

.teaching-hero__subtitle {
	font-size     : 20px;
	font-weight   : 600;
	line-height   : 30px;
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {
		font-size   : 24px;
		line-height : 32px;
	}
	@media screen and (min-width : $break-desktop) {

	}

}

.teaching-hero__wrap {
	align-items    : flex-start;
	display        : flex;
	flex-direction : column;

	@media screen and (min-width : $break-table) {
		flex-direction : row;
	}

	.b-btn + .b-btn {
		margin-left : 0;
		margin-top  : 12px;

		@media screen and (min-width : $break-table) {
			margin-left : 12px;
			margin-top  : 0;
		}
		@media screen and (min-width : $break-desktop) {

		}
	}
}
