.courses-cta {
	padding : 48px 0;

	@media screen and (min-width : $break-table) {
		padding : 72px 0;
	}

	@media screen and (min-width : $break-desktop) {

	}

	&--primary {
		background-color : $primary-color;

		.courses-cta__list {
			li {
				span {
					color : $light-color;
				}

				&:before {
					background-image : url("../images/icon/chevron-right-white.svg");
				}
			}
		}
	}

	&--secondary {
		background-color : $secondary-color;
	}
}

.courses-cta__main {
	margin-bottom : 48px;
	text-align    : center;

	@media screen and (min-width : $break-table) {
		margin-bottom : 72px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.courses-cta__title {
	color     : $light-color;
	margin    : 0 auto 40px auto;
	max-width : 730px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.courses-cta__buttons {
	align-items    : center;
	display        : flex;
	flex-direction : column;

	.b-btn + .b-btn {
		margin-left : 0;
		margin-top  : 24px;
	}


	@media screen and (min-width : $break-table) {
		flex-direction  : row;
		justify-content : center;

		.b-btn + .b-btn {
			margin-left : 24px;
			margin-top  : 0;
		}
	}

	@media screen and (min-width : $break-desktop) {

	}

}

.courses-cta__list {
	margin    : 0 auto;
	max-width : 176px;

	@media screen and (min-width : $break-table) {
		display         : flex;
		justify-content : center;
		max-width       : none;
	}

	@media screen and (min-width : $break-desktop) {

	}


	li {
		position : relative;

		&:not(:last-child) {
			margin-bottom : 10px;
		}

		@media screen and (min-width : $break-table) {
			margin-bottom : 0;

			&:not(:first-child) {
				margin-left : 46px;
			}
		}

		&:before {
			background-image  : url("../images/icon/chevron-right.svg");
			background-repeat : no-repeat;
			background-size   : 18px 19px;
			content           : "";
			height            : 19px;
			left              : -32px;
			position          : absolute;
			top               : 3px;
			width             : 18px;
			z-index           : 1;
		}


		span {
			color       : $dark-color;
			font-size   : 14px;
			font-weight : 400;
			line-height : 24px;
		}
	}
}
