// -----------------------------------------------
//  Button config
// -----------------------------------------------

$btn-font-weight             : 600;
$btn-line-height             : 24px;
$btn-border-width            : 1px;
$btn-padding                 : 12px 32px;
$btn-font-size               : 16px;
$btn-border-radius           : 0;
$btn-transition              : background .3s ease, color .3s ease, border-color .3s ease;

// OUTLINE

// SIZE
// Big
$btn-padding-big             : 12px 32px;
$btn-font-size-big           : 16px;
$btn-svg-width-big           : 24px;
$btn-svg-height-big          : 24px;
$btn-padding-icon-big        : 12px 12px;

// Medium
$btn-padding-md              : 10px 20px;
$btn-font-size-md            : 14px;
$btn-svg-width-md            : 20px;
$btn-svg-height-md           : 20px;
$btn-padding-icon-md         : 10px 10px;

// Small
$btn-padding-small           : 6px 12px;
$btn-font-size-small         : 12px;
$btn-svg-width-sm            : 16px;
$btn-svg-height-sm           : 16px;
$btn-padding-icon-md         : 8px 8px;


// COLOR

// Primary
$btn-primary-bg              : #6734EA;
$btn-primary-color           : #FFF;
$btn-primary-color-outline   : rgba(141, 103, 239, 1);
$btn-primary-bg-hover        : #8D67EF;
$btn-primary-color-hover     : #FFF;
$btn-primary-bg-dis          : #8D67EF;

// Secondary
$btn-secondary-bg            : #63C872;
$btn-secondary-color         : #000;
$btn-secondary-color-outline : $btn-secondary-bg;
$btn-secondary-bg-hover      : #4A9655;
$btn-secondary-color-hover   : #000;

// White
$btn-white-bg                : #FFF;
$btn-white-color             : #6734EA;
$btn-white-color-outline     : $btn-white-bg;
$btn-white-bg-hover          : darken($btn-white-bg, 10%);
$btn-white-color-hover       : #6734EA;

// Black
$btn-black-bg                : #000;
$btn-black-color             : #63C872;
$btn-black-color-outline     : $btn-black-bg;
$btn-black-bg-hover          : $btn-black-bg;
$btn-black-color-hover       : darken(#63C872, 15%);

// Link
$btn-link-bg                 : transparent;
$btn-link-color              : #18A3FA;
$btn-link-bg-hover           : #F7F7F7;
$btn-link-color-hover        : darken(#18A3FA, 10%);

// SPACING
$btn-spacing-x               : 5px;
$btn-spacing-y               : 5px;

// -----------------------------------------------
//  Button style
// -----------------------------------------------

.b-btn {
	align-items        : center;
	border             : $btn-border-width solid transparent;
	border-radius      : $btn-border-radius;
	-moz-box-sizing    : border-box;
	-webkit-box-sizing : border-box;
	box-sizing         : border-box;
	display            : inline-flex;
	flex-flow          : row nowrap;
	font-size          : $btn-font-size;
	font-weight        : $btn-font-weight;
	justify-content    : center;
	line-height        : $btn-line-height;
	padding            : $btn-padding;
	position           : relative;
	text-align         : center;
	text-decoration    : none;
	transition         : $btn-transition;
	user-select        : none;
	vertical-align     : middle;
	white-space        : nowrap;

	&--small {
		font-size : $btn-font-size-small;
		padding   : $btn-padding-small;
	}

	&--arrow {
		fill        : currentColor;
		height      : $btn-svg-height-big;
		margin-left : 16px;
		width       : $btn-svg-width-big;

		path {
			fill : currentColor;
		}
	}

	&--phone {
		fill : currentColor;

		path {
			fill : currentColor;
		}
	}

	&:hover,
	&:focus-within,
	&.focus {
		outline : 0;
	}

	&.disabled,
	&:disabled {
		box-shadow : none;
		cursor     : default;
		opacity    : .45;
	}

	&:active,
	&.active {

	}

	[class^="b-icon-"],
	[class*=" b-icon-"] {
		margin-right   : 10px;
		vertical-align : middle;
	}

	// SIZE
	// Big
	&--big {
		font-size : $btn-font-size-big;
		padding   : $btn-padding-big;
	}

	&--small {
		font-size : $btn-font-size-small;
		padding   : $btn-padding-small;
	}

	// COLOR

	// Primary
	&--primary {
		background-color : $btn-primary-bg;
		color            : $btn-primary-color;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : $btn-primary-bg-hover;
			color            : $btn-primary-color-hover;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $btn-primary-bg;
				color            : $btn-primary-color;
			}
		}
	}

	// Secondary
	&--secondary {
		background-color : $btn-secondary-bg;
		color            : $btn-secondary-color;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : $btn-secondary-bg-hover;
			color            : $btn-secondary-color-hover;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $btn-secondary-bg;
				color            : $btn-secondary-color;
			}
		}
	}

	// White
	&--white {
		background-color : $btn-white-bg;
		color            : $btn-white-color;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : $btn-white-bg;
			color            : #8D67EF;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $btn-white-bg;
				color            : $btn-white-color;
			}
		}
	}

	// Black
	&--black {
		background-color : $btn-black-bg;
		color            : $btn-black-color;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : $btn-black-bg-hover;
			color            : $btn-black-color-hover;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $btn-black-bg;
				color            : $btn-black-color;
			}
		}
	}

	// OUTLINE

	// Primary
	&--outline-primary {
		background-color : transparent;
		border           : 1px solid rgba(141, 103, 239, 1);
		color            : $primary-color;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : transparent;
			border-color     : rgba(103, 52, 234, 1);
			color            : $primary-color;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : transparent;
				border-color     : $btn-primary-bg;
				color            : $btn-primary-color-outline;
			}
		}
	}


	// Secondary
	&--outline-secondary {
		background-color : transparent;
		border           : 1px solid #4A9655;
		color            : #4A9655;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : transparent;
			border-color     : #63C872;
			color            : #4A9655;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : transparent;
				border-color     : $btn-secondary-bg;
				color            : $btn-secondary-color-outline;
			}
		}
	}

	// White
	&--outline-white {
		background-color : transparent;
		border           : 1px solid rgba(255, 255, 255, 0.65);;
		color            : $btn-white-color-outline;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : transparent;
			border-color     : #FFF;
			color            : $btn-white-color-outline;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : transparent;
				border-color     : $btn-white-bg;
				color            : $btn-white-color-outline;
			}
		}
	}

	// Black
	&--outline-black {
		background-color : transparent;
		border           : 1px solid rgba(0, 0, 0, 0.6);
		color            : $btn-black-color-outline;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : transparent;
			border-color     : $dark-color;
			color            : $btn-black-color-outline;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : transparent;
				border-color     : $btn-black-bg;
				color            : $btn-black-color-outline;
			}
		}
	}


	//Text

	// Primary
	&--text-primary {
		background-color : transparent;
		border           : transparent;
		color            : $primary-color;
		padding          : 0;

		&:after {
			background-color : $primary-color;
			bottom           : -4px;
			content          : "";
			height           : 1px;
			left             : 0;
			position         : absolute;
			transition       : width .2s ease-in-out;
			width            : 0;
		}

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : transparent;
			color            : $primary-color;

			&:after {
				transition : width .2s ease-in-out;
				width      : 100%;
			}
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : transparent;
				border-color     : $btn-primary-bg;
				color            : $btn-primary-color-outline;
			}
		}
	}

	// Secondary
	&--text-secondary {
		background-color : transparent;
		border           : transparent;
		color            : $secondary-color;
		padding          : 0;

		&:after {
			background-color : $secondary-color;
			bottom           : 0;
			content          : "";
			height           : 1px;
			left             : 0;
			position         : absolute;
			transition       : width .2s ease-in-out;
			width            : 0;
		}

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : transparent;
			color            : $secondary-color;

			&:after {
				transition : width .2s ease-in-out;
				width      : 100%;
			}
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : transparent;
				border-color     : $btn-secondary-bg;
				color            : $btn-secondary-color-outline;
			}
		}
	}

	// White
	&--text-white {
		background-color : transparent;
		border           : transparent;
		color            : $light-color;
		padding          : 0;

		&:after {
			background-color : $light-color;
			bottom           : 0;
			content          : "";
			height           : 1px;
			left             : 0;
			position         : absolute;
			transition       : width .2s ease-in-out;
			width            : 0;
		}

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : transparent;
			color            : $light-color;

			&:after {
				transition : width .2s ease-in-out;
				width      : 100%;
			}
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : transparent;
				border-color     : transparent;
				color            : transparent;
			}
		}
	}

	// Black
	&--text-black {
		background-color : transparent;
		border           : transparent;
		color            : $dark-color;
		padding          : 0;

		&:after {
			background-color : $dark-color;
			bottom           : 0;
			content          : "";
			height           : 1px;
			left             : 0;
			position         : absolute;
			transition       : width .2s ease-in-out;
			width            : 0;
		}

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : transparent;
			color            : $dark-color;

			&:after {
				transition : width .2s ease-in-out;
				width      : 100%;
			}
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : transparent;
				border-color     : transparent;
				color            : transparent;
			}
		}
	}

	//Icon
	// Primary
	&--icon-primary {
		background-color : $btn-primary-bg;
		color            : $btn-primary-color;
		padding          : $btn-padding-icon-big;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : #8D67EF;
			color            : $btn-primary-color-hover;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $btn-primary-bg;
				color            : $btn-primary-color;
			}
		}
	}

	&--icon-primary-transparent {
		background-color : transparent;
		border           : 1px solid #8D67EF;
		color            : $btn-primary-bg;
		padding          : $btn-padding-icon-big;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : transparent;
			border-color     : #6734EA;
			color            : $btn-primary-bg;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $btn-primary-bg;
				color            : $btn-primary-color;
			}
		}
	}


	// Secondary
	&--icon-secondary {
		background-color : $btn-secondary-bg;
		color            : $btn-secondary-color;
		padding          : $btn-padding-icon-big;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : #4A9655;
			color            : $btn-secondary-color-hover;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $btn-secondary-bg;
				color            : $btn-secondary-color;
			}
		}
	}

	&--icon-secondary-transparent {
		background-color : transparent;
		border           : 1px solid #4A9655;
		color            : $btn-secondary-bg;
		padding          : $btn-padding-icon-big;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : transparent;
			border-color     : #63C872;
			color            : $btn-secondary-bg;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $btn-secondary-bg;
				color            : $btn-secondary-color;
			}
		}
	}

	// White
	&--icon-white {
		background-color : $btn-white-bg;
		color            : $btn-white-color;
		padding          : $btn-padding-icon-big;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : $btn-white-bg;
			color            : #8D67EF;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $btn-white-bg;
				color            : $btn-white-color;
			}
		}
	}

	&--icon-white-transparent {
		background-color : transparent;
		border           : 1px solid rgba(255, 255, 255, 0.65);
		color            : $btn-white-bg;
		padding          : $btn-padding-icon-big;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : transparent;
			border-color     : $light-color;
			color            : $btn-white-bg;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $btn-white-bg;
				color            : $btn-white-color;
			}
		}
	}

	// Black
	&--icon-black {
		background-color : $btn-black-bg;
		color            : $btn-black-color;
		padding          : $btn-padding-icon-big;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : $btn-black-bg;
			color            : #4A9655;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $btn-black-bg;
				color            : $btn-black-color;
			}
		}
	}

	&--icon-black-transparent {
		background-color : transparent;
		border           : 1px solid rgba(0, 0, 0, 0.6);
		color            : $btn-black-bg;
		padding          : $btn-padding-icon-big;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : transparent;
			border-color     : $dark-color;
			color            : $btn-black-bg;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $btn-black-bg;
				color            : $btn-black-color;
			}
		}
	}

	// FULL-SIZE
	&--full-size {
		display : block;
		width   : 100%;

		+ .full-size {
			margin-right : $btn-spacing-y;
		}
	}

	// SPACING
	& + .b-btn {
		margin-left : 12px;
	}
}

.b-btn-grouping {
	display : inline-block;

	.b-btn {
		border-radius : 0;
		float         : left;
		margin-right  : 0;

		&:first-child {
			border-radius : $btn-border-radius 0 0 $btn-border-radius;
		}

		&:last-child {
			border-radius : 0 $btn-border-radius $btn-border-radius 0;
		}

		&.outline {
			margin-left : -1px;
			z-index     : 1;

			&:hover {
				z-index : 2;
			}

			&.active {
				z-index : 3;
			}

			&:first-child {
				margin-left : 0;
			}
		}
	}
}

@keyframes loadingSpin {
	0% {
		transform : rotate(0deg);
	}
	100% {
		transform : rotate(360deg);
	}
}

// Future-proof disabling of clicks on `<a>` elements
a.b-btn.disabled,
fieldset[disabled] a.b-btn {
	pointer-events : none;
}

// Specificity overrides
input[type="submit"],
input[type="reset"],
input[type="button"] {
	&.full-size {
		width : 100%;
	}
}
