.fees-complete {
	padding : 56px 0;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {

	}
}

.fees-complete__title {
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		max-width : 444px;
	}
}

.fees-complete__wrapper {


	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		align-items     : flex-start;
		display         : flex;
		justify-content : space-between;
	}
}

.fees-complete__left {
	margin-bottom : 30px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 0;
		margin-right  : 30px;
		max-width     : 475px;
	}
}

.fees-complete__right {


	@media screen and (min-width : $break-table) {
		display : flex;
	}
	@media screen and (min-width : $break-desktop) {
		padding-top : 92px;
	}
}

.fees-complete__about {
	color         : $dark-color;
	font-size     : 18px;
	font-weight   : 400;
	line-height   : 32px;
	margin-bottom : 40px;
	max-width     : 444px;
}


.fees-complete__item {
	align-items : flex-start;
	display     : flex;

	&:not(:last-child) {
		margin-bottom : 24px;
	}
}

.fees-complete__name {
	color         : $dark-color;
	font-size     : 24px;
	font-weight   : 600;
	line-height   : 32px;
	margin-bottom : 8px;
}

.fees-complete__text {
	color       : $dark-color;
	font-size   : 16px;
	font-weight : 400;
	line-height : 26px;
}

.fees-complete__icon {
	flex-shrink  : 0;
	height       : 32px;
	margin-right : 24px;
	width        : 32px;
}

.fees-complete--single {
	.fees-complete__wrapper {

		@media screen and (min-width : $break-table) {
			align-items     : center;
			display         : flex;
			justify-content : space-around;
		}
		@media screen and (min-width : $break-desktop) {

		}
	}

	.fees-complete__left {
		max-width : 540px;

		@media screen and (min-width : $break-table) {
			margin-right : 30px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	.fees-complete__about {
		max-width : none;
	}

	.fees-complete__right {
		padding-top : 0;
	}

	.fees-card {


		@media screen and (min-width : $break-table) {
			min-width : 340px;
		}

		@media screen and (min-width : $break-desktop) {
			min-width : 350px;
		}
	}

	.fees-complete__grid {
		margin-bottom : 40px;
	}
}

