
.overview__top {
	margin-bottom : 36px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 48px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.overview__text {
	display : none;

	@media screen and (min-width : $break-desktop) {
		display : block;
	}
}

.overview__grid {
	display               : grid;
	grid-row-gap          : 24px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-gap              : 36px 30px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap : 48px 30px;
	}
}

.overview__item {
	align-items : flex-start;
	display     : flex;
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
	}

	@media screen and (min-width : $break-desktop) {
		flex-direction : column;
		grid-column    : 4 span;
	}
}

.overview__icon {
	flex-shrink  : 0;
	margin-right : 24px;

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 24px;
		margin-right  : 0;
	}
}

.overview__name {
	color       : $dark-color;
	font-size   : 24px;
	font-weight : 600;
	line-height : 32px;
}
