.hero {
	background-image  : url("../images/index/hero/bg-hero.webp");
	background-repeat : no-repeat;
	background-size   : 1980px;
	padding           : 20px 0 40px;

	.no-webp & {
		background-image : url("../images/index/hero/bg-hero.jpg");
	}

	@media screen and (min-width : $break-table) {
		padding : 40px 0 48px;
	}

	@media screen and (min-width : $break-desktop) {
		background-size : cover;
		padding         : 80px 0 48px;
	}
}

.hero__wrapper {
	max-width : 730px;
}

.hero-title {
	color         : $light-color;
	font-size     : 40px;
	font-weight   : 800;
	line-height   : 54px;
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {
		font-size   : 72px;
		line-height : 98px;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.hero__title {

}

.hero-text {
	color         : $light-color;
	font-size     : 18px;
	font-weight   : 400;
	line-height   : 32px;
	margin-bottom : 16px;
	max-width     : 620px;
}

.hero-subtitle {
	color         : $light-color;
	font-size     : 24px;
	font-weight   : 600;
	line-height   : 32px;
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		font-size   : 28px;
		font-weight : 700;
		line-height : 40px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.hero__courses {
	display        : flex;
	flex-direction : column;

	@media screen and (min-width : $break-table) {
		flex-direction  : row;
		justify-content : space-between;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.mini-courses {
	background-color : $dark-color;
	padding          : 24px 24px;
	position         : relative;
	width            : 100%;

	&:last-child {
		margin-top : 30px;
	}

	@media screen and (min-width : $break-table) {
		width : 50%;

		&:last-child {
			margin-left : 30px;
			margin-top  : 0;
		}
	}

	&__title {
		color         : $light-color;
		font-size     : 24px;
		font-weight   : 600;
		line-height   : 32px;
		margin-bottom : 24px;
	}

	&__list {
		margin-bottom : 24px;
	}

	&__item {
		&:not(:last-child) {
			margin-bottom : 16px;
		}
	}

	&__about {
		color          : rgba(255, 255, 255, 0.65);
		display        : block;
		font-size      : 14px;
		font-weight    : 700;
		letter-spacing : 1px;
		line-height    : 18px;
		text-transform : uppercase;
	}

	&__name {
		color       : $light-color;
		font-size   : 18px;
		font-weight : 600;
		line-height : 28px;
	}

	&__img {
		position : absolute;
		right    : 24px;
		top      : 24px;
		z-index  : 1;
	}
}
