.accordion-mini__content {
	box-sizing  : content-box;
	color       : $dark-color;
	font-size   : 14px;
	font-weight : 400;
	line-height : 24px;
	max-height  : 0;
	opacity     : 0;
	overflow    : hidden;
	padding     : 0 20px;
	transition  : all 0.3s ease-out;
	will-change : max-height;


	@media screen and (min-width : $break-table) {
		font-size   : 18px;
		line-height : 32px;
		padding     : 0 32px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.accordion-mini__control {
	align-items      : center;
	background-color : transparent;
	border           : none;
	border-bottom    : 1px solid rgba(0, 0, 0, 0.2);
	color            : #000;
	cursor           : pointer;
	display          : flex;
	font-size        : 14px;
	font-weight      : 600;
	justify-content  : space-between;
	letter-spacing   : 0.3px;
	line-height      : 20px;
	padding          : 10px 20px;
	text-align       : left;
	transition       : transform .3s ease, background-color .3s ease;
	width            : 100%;

	&:hover {
		background-color : #F4F5F7;
	}


	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		line-height : 24px;
		padding     : 16px 32px;
	}

	@media screen and (min-width : $break-desktop) {

	}

}

.accordion-mini__icon {
	transition : transform .3s ease;
}

.accordion-mini.open .accordion-mini__content {
	opacity : 1;
	padding : 0 20px 10px 20px;

	@media screen and (min-width : $break-table) {
		padding : 0 32px 16px 32px;
	}
}

.accordion-mini.open .accordion-mini__control {
	border-bottom : transparent;
}

.accordion-mini.open .accordion-mini__content {
	border-bottom : 1px solid rgba(0, 0, 0, 0.2);
}

.accordion-mini.open .accordion-mini__icon {
	transform : rotate(180deg);
}
