.faq {
}

.faq__wrapper {
	position : relative;


	@media screen and (min-width : $break-table) {
	}

	@media screen and (min-width : $break-desktop) {
		display : flex;
	}
}

.faq__title {
	margin-bottom : 20px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 8px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 0;
	}
}

.faq__text {
	color         : $dark-color;
	font-size     : 18px;
	font-weight   : 400;
	line-height   : 32px;
	margin-bottom : 20px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 0;
	}
}

.faq__left {


	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		width : 50%;
	}
}

.faq__right {


	@media screen and (min-width : $break-table) {
		width : 524px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-left : 30px;
		width       : 50%;
	}
}

.faq__dots {
	display : none;
	@media screen and (min-width : $break-table) {
		bottom   : 0;
		display  : block;
		position : absolute;
		right    : 0;
		z-index  : 1;
	}

	@media screen and (min-width : $break-desktop) {
		margin-top : 10px;
		position   : unset;
	}
}


.faq__left {
	display         : flex;
	flex-direction  : column;
	justify-content : space-between;
}
