.faculty-large {
	background-color : $yellow;
}

.faculty-large__top {
	margin-bottom : 20px;
	text-align    : center;

	@media screen and (min-width : $break-table) {
		margin-bottom : 40px;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.faculty-large__grid {
	display               : grid;
	grid-gap              : 20px;
	grid-template-columns : repeat(4, 1fr);
	margin-bottom         : 20px;

	@media screen and (min-width : $break-table) {
		grid-gap      : 30px;
		margin-bottom : 40px;
	}
	@media screen and (min-width : $break-desktop) {
		grid-template-columns : repeat(12, 1fr);
	}
}

.faculty-large__item {
	grid-column : 4 span;
}

.faculty-large__wrapper {
	@media screen and (min-width : $break-table) {
		margin    : 0 auto;
		max-width : 524px;
	}
	@media screen and (min-width : $break-desktop) {
		max-width : none;
	}

	.b-btn--outline-black {
		left      : 50%;
		position  : relative;
		transform : translateX(-50%);
	}
}

.faculty-large__info {
	background-color : #FAE4A2;
	margin-bottom    : 35px;
	padding          : 20px 16px;
	position         : relative;
	text-align       : center;


	@media screen and (min-width : $break-table) {
		padding : 40px 28px;
	}
	@media screen and (min-width : $break-desktop) {

	}

	&:before {
		border-bottom : 13px solid transparent;
		border-left   : 22px solid #FAE4A2;
		border-top    : 13px solid transparent;
		bottom        : -18px;
		content       : "";
		display       : block;
		height        : 0;
		left          : 50%;
		position      : absolute;
		transform     : translateX(-50%) rotate(330deg);
		width         : 0;
	}
}

.faculty-large__name {
	color       : $dark-color;
	font-size   : 24px;
	font-weight : 600;
	line-height : 32px;
}

.faculty-large__about {
	color       : $dark-color;
	font-size   : 14px;
	font-weight : 400;
	line-height : 24px;
}

.faculty-large__profile {
	text-align : center;

	h4 {
		color       : $dark-color;
		font-size   : 18px;
		font-weight : 700;
		line-height : 28px;
	}

	p {
		color       : rgba(0, 0, 0, 0.6);
		font-size   : 14px;
		font-weight : 400;
		line-height : 24px;
	}
}

.faculty-large__img {
	border-radius : 100%;
	height        : 56px;
	margin        : 0 auto 8px auto;
	overflow      : hidden;
	width         : 56px;

	img {
		height     : 100%;
		object-fit : cover;
		width      : 100%;
	}
}
