.legal {
	overflow : hidden;
	padding  : 68px 0 48px 0;

	@media screen and (min-width : $break-table) {
		padding : 48px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 56px 0;
	}
}

.legal__wrapper {
	margin    : 0 auto;
	max-width : 730px;
}

.legal__head {
	margin-bottom : 30px;
}

.legal__item {
	&:not(:last-child) {
		margin-bottom : 30px;
	}
}

.legal__name {
	color         : $dark-color;
	font-size     : 28px;
	font-weight   : 800;
	line-height   : 48px;
	margin-bottom : 8px;

	&--big {
		font-size   : 48px;
		line-height : 54px;
	}

	&--sm {
		font-size   : 24px;
		line-height : 32px;
	}
}

.legal__text {
	color       : $dark-color;
	font-size   : 16px;
	line-height : 26px;

	&:not(:last-child) {
		margin-bottom : 30px;
	}
}

.legal__table {
	margin-right : -20px;
	overflow-x   : auto;

	@media screen and (min-width : $break-table) {
		margin-right : 0;
	}

	table {
		max-width : none;
		width     : 600px;
		@media screen and (min-width : $break-table) {
			max-width : 100%;
			width     : 100%;
		}

		@media screen and (min-width : $break-desktop) {

		}

		thead {
			background-color : #F0F0F5;
			font-size        : 14px;

			tr {
				outline : 1px solid rgba(0, 0, 0, 0.2);
			}
		}

		tbody {
			tr {
				font-size : 14px;

				&:nth-child(2n) {
					background-color : #F0F0F5;

					td {
						border-bottom : 1px solid rgba(0, 0, 0, 0.2);
					}
				}
			}
		}

		td, th {
			padding : 10px 20px;
		}
	}
}
