.way-learning {
	
	.b-btn--primary {
		left      : 50%;
		position  : relative;
		transform : translateX(-50%);
	}
}

.way-learning__wrapper {
	margin-bottom : 36px;
	text-align    : center;

	@media screen and (min-width : $break-table) {
		margin-bottom : 48px;
	}
	@media screen and (min-width : $break-desktop) {

	}

}

.way-learning__title {
	margin-bottom : 8px;
}

.way-learning__grid {
	display               : grid;
	grid-row-gap          : 24px;
	grid-template-columns : repeat(4, 1fr);
	margin-bottom         : 36px;

	@media screen and (min-width : $break-table) {
		grid-column-gap       : 48px;
		grid-row-gap          : 48px;
		grid-template-columns : repeat(8, 1fr);
		margin-bottom         : 48px;
	}
	@media screen and (min-width : $break-desktop) {
		grid-column-gap       : 30px;
		grid-template-columns : repeat(12, 1fr);
	}
}

.way-learning__item {
	grid-column : 4 span;
	text-align  : center;
}

.way-learning__icon {
	margin-bottom : 24px;
}

.way-learning__name {
	color         : $dark-color;
	font-size     : 24px;
	font-weight   : 600;
	line-height   : 32px;
	margin-bottom : 8px;
}

.way-learning__about {
	font-size   : 16px;
	font-weight : 400;
	line-height : 26px;
}
