.banner {
	background-color : $blue;
	bottom           : 0;
	display          : none;
	left             : 0;
	padding          : 16px 0;
	position         : fixed;
	width            : 100%;
	z-index          : 80;

	&.active {
		display : block;
	}
}

.banner__wrapper {
	align-items           : center;
	display               : grid;
	grid-gap              : 16px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap : 30px;
	}
}

.banner__wrap {
	align-items    : flex-start;
	display        : flex;
	flex-direction : column;
	grid-column    : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 2/11;
	}

	@media screen and (min-width : $break-desktop) {
		align-items     : center;
		flex-direction  : row;
		grid-column     : 3/11;
		justify-content : space-between;
	}

	.b-btn--outline-white {
		padding : 10px 20px;
	}
}

.banner__info {
	margin-bottom : 12px;

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 0;
	}
}

.banner__title {
	color          : rgba(255, 255, 255, 0.65);
	font-size      : 14px;
	font-weight    : 700;
	letter-spacing : 1px;
	line-height    : 18px;
	text-transform : uppercase;
}

.banner__text {
	color       : $light-color;
	font-size   : 14px;
	font-weight : 600;
	line-height : 24px;
}

.banner__dots {
	display : none;
	@media screen and (min-width : $break-table) {
		align-self  : start;
		display     : block;
		grid-column : 1/1;
		margin-left : 23px;
	}

	@media screen and (min-width : $break-desktop) {
		align-self  : center;
		grid-column : 2/2;
		margin-left : 49px;
	}
}

.banner__close {
	align-self       : start;
	background-color : transparent;
	border           : none;
	cursor           : pointer;
	grid-column      : 4/4;
	height           : 24px;
	margin-left      : 38px;
	padding          : 0;
	width            : 24px;

	@media screen and (min-width : $break-table) {
		grid-column : 12/12;
	}

	@media screen and (min-width : $break-desktop) {
		align-self : center;
	}

	img {
		pointer-events : none;
	}
}
