.inclusive {
	
}

.inclusive__wrapper {

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		display         : flex;
		justify-content : space-between;
	}

	.b-btn--primary {
		padding : $btn-padding-md;
	}
}

.inclusive__gallery {
	display               : grid;
	grid-column-gap       : 12px;
	grid-row-gap          : 20px;
	grid-template-columns : repeat(12, 1fr);


	@media screen and (min-width : $break-table) {
		padding : 0 84px;
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap : 20px;
		padding  : 0;
		width    : 50%;
	}

	picture {
		img {
			max-width : none;
			width     : 100%;
		}

		&:nth-child(1) {
			grid-column : 1/7;
		}

		&:nth-child(2) {
			align-self  : self-end;
			grid-column : 7/13;
			grid-row    : 1/2;
		}

		&:nth-child(3) {
			grid-column : 1/5;
			grid-row    : 2/2;
		}

		&:nth-child(4) {
			grid-column : 5/13;
			grid-row    : 2/2;
		}

	}
}

.inclusive__content {
	padding-top : 30px;

	@media screen and (min-width : $break-table) {
		padding-left : 52px;
	}

	@media screen and (min-width : $break-desktop) {
		padding-left : 78px;
		padding-top  : 89px;
		width        : 50%;
	}
}

.inclusive__title {
	color         : $dark-color;
	font-size     : 28px;
	font-weight   : 700;
	line-height   : 40px;
	margin-bottom : 16px;


	@media screen and (min-width : $break-table) {
		font-size   : 40px;
		font-weight : 800;
		line-height : 54px;
	}

	@media screen and (min-width : $break-desktop) {
		max-width : 450px;
	}
}

.inclusive__text {
	color         : $dark-color;
	font-size     : 18px;
	font-weight   : 400;
	line-height   : 32px;
	margin-bottom : 20px;

	@media screen and (min-width : $break-table) {
		font-size     : 18px;
		font-weight   : 400;
		line-height   : 32px;
		margin-bottom : 40px;
	}

	@media screen and (min-width : $break-desktop) {
		max-width : 450px;
	}
}


.feature-mini {
	display : flex;

	&:not(:last-child) {
		margin-bottom : 24px;
	}

	&__icon {
		flex-shrink  : 0;
		margin-right : 28px;
	}

	&__title {
		color         : $dark-color;
		font-size     : 20px;
		font-weight   : 600;
		line-height   : 30px;
		margin-bottom : 4px;
	}

	&__text {
		color         : $dark-color;
		font-size     : 16px;
		font-weight   : 400;
		line-height   : 26px;
		margin-bottom : 4px;
	}

	.b-btn--text-primary {
		.b-btn--arrow {
			height      : 20px;
			margin-left : 8px;
			width       : 20px;
		}
	}
}

.inclusive__wrap {
	margin-bottom : 40px;
}
