.lead {
	padding : 44px 0 96px 0;

	@media screen and (min-width : $break-table) {
		padding : 24px 0 96px 0;
	}
}

.lead__wrapper {
	margin    : 0 auto;
	max-width : 540px;
}

.lead__top {
	margin-bottom : 30px;
}

.lead__title {
	color       : $dark-color;
	font-size   : 28px;
	font-weight : 700;
	line-height : 40px;


	@media screen and (min-width : $break-table) {
		font-size   : 48px;
		font-weight : 800;
		line-height : 64px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.lead__text {
	color       : $dark-color;
	font-size   : 18px;
	font-weight : 400;
	line-height : 32px;
}
