.app {
	overflow       : hidden;
	padding-bottom : 40px;
	padding-top    : 350px;

	@media screen and (min-width : $break-table) {
		padding-bottom : 200px;
		padding-top    : 200px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.app__wrapper {
	background-color : $primary-color;
	border-radius    : 16px;
	padding          : 80px 20px 20px 20px;
	position         : relative;

	@media screen and (min-width : $break-table) {
		padding : 40px 0 40px 48px;
		width   : 900px;
	}

	@media screen and (min-width : $break-desktop) {
		width : auto;
	}
}

.app__title {
	color         : $light-color;
	font-size     : 28px;
	font-weight   : 700;
	line-height   : 40px;
	margin-bottom : 24px;
	@media screen and (min-width : $break-table) {
		font-size     : 40px;
		font-weight   : 800;
		line-height   : 54px;
		margin-bottom : 32px;

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.app__conent {

	@media screen and (min-width : $break-table) {
		max-width : 383px;
	}

	@media screen and (min-width : $break-desktop) {
		max-width : 490px;
	}
}

.app__text {
	color         : $light-color;
	font-size     : 18px;
	font-weight   : 400;
	line-height   : 32px;
	margin-bottom : 24px;
	max-width     : 490px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 32px;

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.app__img {
	//flex-shrink : 0;
	//margin-left : 142px;

	height    : 400px;
	left      : 50%;
	position  : absolute;
	right     : 0;
	top       : -300px;
	transform : translateX(-50%);
	width     : 200px;
	z-index   : 1;

	@media screen and (min-width : $break-table) {

		height    : 700px;
		left      : unset;
		right     : 78px;
		top       : 50%;
		transform : translateY(-50%);
		width     : 380px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}
