.questions {

}

.questions__wrapper {

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		display : flex;
	}
}

.questions__img {

	background-image  : url("../images/index/questions/questions-mob.jpg");
	background-repeat : no-repeat;
	background-size   : cover;
	min-height        : 240px;


	@media screen and (min-width : $break-table) {
		background-image : url("../images/index/questions/questions-md.jpg");
	}

	@media screen and (min-width : $break-desktop) {
		background-image : url("../images/index/questions/questions-desk.jpg");
		flex-shrink      : 0;
		min-height       : unset;
		width            : 350px;
	}
}

.questions__right {
	background-color : $dark-color;
	padding          : 20px 20px;
	width            : 100%;

	@media screen and (min-width : $break-table) {
		padding : 40px 40px;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 40px 40px 40px 30px;
	}

	.b-btn--secondary {
		font-size   : 16px;
		line-height : 24px;
		padding     : $btn-padding-big;
	}
}

.questions__title {
	color         : $light-color;
	font-size     : 28px;
	font-weight   : 700;
	line-height   : 40px;
	margin-bottom : 24px;
}

.questions__text {
	color         : $light-color;
	font-size     : 18px;
	font-weight   : 400;
	line-height   : 32px;
	margin-bottom : 24px;

	a {
		color    : $light-color;
		position : relative;

		&:after {
			background-color : $light-color;
			bottom           : 3px;
			content          : "";
			height           : 1px;
			left             : 0;
			position         : absolute;
			width            : 100%;
			z-index          : 1;
		}
	}
}

.questions__grid {
	display               : grid;
	grid-column-gap       : 30px;
	grid-row-gap          : 24px;
	grid-template-columns : repeat(6, 1fr);
	margin-bottom         : 36px;

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.questions__item {
	align-items : flex-start;
	display     : flex;
	grid-column : 6 span;
}

.questions__icon {
	flex-shrink  : 0;
	margin-right : 16px;
}

.questions__name {
	color                : $light-color;
	font-size            : 20px;
	font-weight          : 600;
	line-height          : 30px;
	margin-bottom        : 4px;
	text-decoration-line : underline;
}

.questions__about {
	color       : $light-color;
	font-size   : 16px;
	font-weight : 400;
	line-height : 26px;
}

.questions__country {
	display : flex;

	li {
		&:not(:first-child) {
			margin-left : 12px;
		}
	}
}
