.feature {

}

.feature__title {
	margin-bottom : 36px;
	max-width     : 750px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 48px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.feature__grid {
	display               : grid;
	grid-gap              : 24px 0;
	grid-template-columns : repeat(6, 1fr);

	@media screen and (min-width : $break-table) {
		grid-gap              : 68px 30px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap : 48px 30px;
	}
}

.feature__item {
	display     : flex;
	grid-column : 6 span;
}


.feature__icon {
	flex-shrink  : 0;
	height       : 60px;
	margin-right : 24px;
	width        : 60px;

	@media screen and (min-width : $break-table) {
		margin-right : 40px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.feature__name {
	color         : $dark-color;
	font-size     : 24px;
	font-weight   : 600;
	line-height   : 32px;
	margin-bottom : 8px;
}

.feature__about {
	color       : $dark-color;
	font-size   : 18px;
	font-weight : 400;
	line-height : 32px;
}
