
.team__wrapper {
	position : relative;

	@media screen and (min-width : $break-table) {
		&:before {
			background-image  : url("../images/icon/more-big.svg");
			background-repeat : no-repeat;
			background-size   : 88px 94px;
			bottom            : 0;
			content           : "";
			height            : 94px;
			position          : absolute;
			right             : 0;
			width             : 88px;
			z-index           : 1;
		}
	}

	@media screen and (min-width : $break-desktop) {
		display         : flex;
		justify-content : space-between;

		&:before {
			left  : 0;
			right : unset;
		}
	}
}

.team__title {
	margin-bottom : 8px;
}

.team__left {
	margin-bottom : 30px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		max-width : 445px;
	}
}

.team__grid {
	background-color      : $primary-color;
	display               : grid;
	grid-row-gap          : 40px;
	grid-template-columns : repeat(6, 1fr);

	padding               : 40px 40px;

	@media screen and (min-width : $break-table) {
		max-width : 524px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-left : 30px;
		max-width   : none;
	}
}

.team__item {
	grid-column : 6 span;
}

.team__profile {
	align-items   : center;
	display       : flex;
	margin-bottom : 8px;
}

.team__photo {
	border-radius : 100%;
	flex-shrink   : 0;
	height        : 56px;
	margin-right  : 16px;
	overflow      : hidden;
	width         : 56px;

	img {
		height     : 100%;
		object-fit : cover;
		width      : 100%;
	}
}

.team__name {
	color       : $light-color;
	font-size   : 18px;
	font-weight : 700;
	line-height : 28px;
}

.team__jobtitle {
	color       : rgba(255, 255, 255, 0.65);
	font-size   : 14px;
	font-weight : 400;
	line-height : 24px;
}

.team__text {
	color         : $light-color;
	font-size     : 14px;
	font-weight   : 400;
	line-height   : 24px;
	margin-bottom : 8px;
}

.team__job {
	align-items   : center;
	display       : flex;
	margin-bottom : 16px;

	div {
		display     : flex;
		flex-shrink : 0;
		width       : 84px;

		&:last-child {
			margin-left : 24px;
		}
	}
}

.team__socials {
	li {
		align-items    : center;
		color          : $light-color;
		display        : flex;
		font-size      : 12px;
		font-weight    : 600;
		letter-spacing : 0.3px;
		line-height    : 16px;

		a {
			height      : 16px;
			margin-left : 12px;
			width       : 16px;
		}
	}
}
