.career {
	padding : 48px 0;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding : 55px 0;
	}
}

.career__wrapper {
	text-align : center;

	@media screen and (min-width : $break-table) {
		align-items     : center;
		display         : flex;
		justify-content : space-between;
		text-align      : unset;
	}

}

.career__title {
	font-size     : 28px;
	font-weight   : 700;
	line-height   : 40px;
	margin-bottom : 8px;


	@media screen and (min-width : $break-desktop) {
		font-size   : 48px;
		font-weight : 800;
		line-height : 64px;
	}
}

.career__text {
	color         : $dark-color;
	font-size     : 18px;
	font-weight   : 400;
	line-height   : 32px;
	margin-bottom : 20px;


	@media screen and (min-width : $break-table) {
		margin-bottom : 0;
		max-width     : 530px;
	}
}

.learning__logo {


	@media screen and (min-width : $break-desktop) {
		flex-shrink : 0;
	}
}
