
.syllabus__head {
	margin-bottom : 24px;
	position      : relative;


	@media screen and (min-width : 768px) {
		&:before {
			background-image  : url("../images/icon/less-icon.svg");
			background-repeat : no-repeat;
			background-size   : 88px 94px;
			content           : "";
			height            : 94px;
			position          : absolute;
			right             : 0;
			top               : 0;
			width             : 88px;
			z-index           : 1;
		}
	}
}

.syllabus__subtitle {
	max-width : 650px;
}

.syllabus__grid {

	display : -webkit-box;
	display : -ms-flexbox;
	display : flex;


	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		grid-template-columns : repeat(12, 1fr);
	}
}


.syllabus-nav {
	display      : none;
	grid-column  : 4 span;
	height       : 100%;
	margin-right : 30px;
	min-width    : 350px;
	position     : -webkit-sticky;
	position     : sticky;
	top          : 174px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		display : block;
	}

	li {
		border-bottom : 1px solid rgba(0, 0, 0, 0.2);
		transition    : background-color .3s ease-in;

		&:hover {
			background-color : #F4F5F7;
		}
	}

	a {
		align-items     : center;
		color           : rgba(0, 0, 0, 0.6);
		display         : flex;
		font-size       : 16px;
		font-weight     : 600;
		justify-content : space-between;
		letter-spacing  : 0.3px;
		line-height     : 24px;
		padding         : 16px 32px;
		width           : 100%;

		svg {
			fill        : currentColor;
			flex-shrink : 0;
		}
	}
}

.syllabus__wrap {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 8 span;
	}
}



