.footer {
	background-color : $dark-color;
	padding          : 48px 0;

	@media screen and (min-width : $break-table) {
		padding : 56px 0 64px 0;
	}
}

.footer__logo {
	display     : block;
	grid-column : 1/3;
	grid-row    : 1/1;

	img {
		height    : 32px;
		max-width : none;
		width     : 164px;
	}
}

.footer__wrapper {
	display               : grid;
	grid-row-gap          : 24px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(12, 1fr);
	}
}

.footer__nav {
	display        : flex;
	flex-direction : column;
	grid-column    : 1/6;
	grid-row       : 2/2;
	max-width      : 480px;
	row-gap        : 12px;

	@media screen and (min-width : $break-table) {
		column-gap     : 24px;
		flex-direction : row;
		flex-wrap      : wrap;
		grid-column    : 1/12;
		row-gap        : 12px;
	}

	li {
		a {
			color       : $secondary-color;
			font-size   : 14px;
			font-weight : 400;
			line-height : 24px;
		}
	}
}

.footer__copyright {
	color       : #D9DBE1;
	font-size   : 14px;
	font-weight : 400;
	grid-column : 1/7;
	grid-row    : 5/5;
	line-height : 24px;

	@media screen and (min-width : $break-table) {
		grid-column : 1/12;
		grid-row    : 3/3;
	}
}

.footer__subnav {
	display        : flex;
	flex-direction : column;
	grid-column    : 1/4;
	grid-row       : 6/6;


	@media screen and (min-width : $break-table) {
		flex-direction : row;
		grid-column    : 1/12;
		grid-row       : 4/4;
	}

	li {
		position : relative;


		&:not(:last-child) {
			margin-bottom : 8px;
		}

		@media screen and (min-width : $break-table) {
			&:not(:first-child) {
				margin-bottom : 0;
				margin-left   : 21px;

				&:after {
					background-color : #969BAB;
					content          : "";
					height           : 12px;
					left             : -11px;
					position         : absolute;
					top              : 8px;
					width            : 1px;
				}
			}

			&:not(:last-child) {
				margin-bottom : unset;
			}
		}


		a {
			color       : #969BAB;
			font-size   : 14px;
			font-weight : 400;
			line-height : 24px;
		}
	}
}

.footer__accreditation {
	grid-column : 1/3;
	grid-row    : 3/3;

	@media screen and (min-width : $break-table) {
		grid-column : 12/13;
		grid-row    : 1/3;
	}

	div {
		color         : #D9DBE1;
		font-size     : 18px;
		font-weight   : 600;
		line-height   : 28px;
		margin-bottom : 16px;
	}

	img {
		-webkit-filter : grayscale(100%);
		filter         : grayscale(100%);
	}
}

.footer__social {
	grid-column : 1/3;
	grid-row    : 4/4;

	@media screen and (min-width : $break-table) {
		grid-column : 12/13;
		grid-row    : 4/4;
	}
}

.s-block {
	display : flex;

	&__link {
		height : 24px;
		width  : 24px;

		&:not(:first-child) {
			margin-left : 16px;
		}
	}
}
